import React from "react";
import _ from "lodash";
import { Row } from "reactstrap";
import cons from "../../../../../constants";
import defaultAvatar from "../../../../../assets/img/theme/default-avatar.png";
import { getBookingFromAppointment } from "../../../../../redux/actions/noteAction";
import { getFileNameFromPath } from "../../../../../helpers/FileHelper";
import { getDateFormat } from "../../../../../helpers/UtilsHelper";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import moment from "moment";
import { Link } from "react-router-dom";

import "./ProviderMessage.scss";

const ProviderMessage = (props) => {
  const { timeCreate, provider, files, message, providerName } = props;

  const providerAvatar =
    _.isNull(provider) || _.isNull(provider.avatar)
      ? defaultAvatar
      : provider.avatar;
  const isNotification =
    message.messageType === cons.MESSAGE_TYPE.APPOINTMENT_MESSAGE
      ? true
      : false;
  return (
    <>
      {isNotification ? (
        <Row className="provider-message d-flex align-items-start justify-content-center">
          {/* <div className="text-center msg-box">
            <div className="notification">
              <RenderContent message={message} />
            </div>
          </div> */}
        </Row>
      ) : (
        <Row className="provider-message d-flex align-items-start justify-content-end">
          <div className="msg-box">
            <div
              className="message-content"
              style={{ background: "#c6eaf5", color: "black" }}
            >
              <RenderContent message={message} />
              <div className={files.length > 0 ? "file-list mt-3" : "d-none"}>
                <b>Attachments</b>
                {files.length > 0 ? <ListFile files={files} /> : ""}
              </div>
            </div>
            <span className="time_date text-right mr-2 mt-2">
              {" "}
              {timeCreate}{" "}
            </span>
          </div>
          <div className="user__avatar ml-2">
            <img
              src={providerAvatar}
              className="avatar rounded-circle"
              alt="avatar"
            />
            <span className="user__avatar__name">{providerName}</span>
          </div>
        </Row>
      )}
    </>
  );
};

const ListFile = (props) => {
  const { files } = props;
  return (
    files.length > 0 &&
    files.map((file, i) => {
      const fileName = getFileNameFromPath(file.messageFile);
      return (
        <div key={i} className="d-flex justify-content-start">
          <a
            className="file-name"
            href={file.messageFile}
            target="_blank"
            rel="noopener noreferrer"
          >
            <li>{fileName}</li>
          </a>
        </div>
      );
    })
  );
};

const RenderContent = (props) => {
  const { message } = props;
  switch (Number(message.messageType)) {
    case cons.MESSAGE_TYPE.DEFAULT_MESSAGE:
      return <RenderManualMessage content={message.content} />;
    // case cons.MESSAGE_TYPE.APPOINTMENT_MESSAGE:
    //   return <RenderAppointmentMessage content={message.content} />;
    default:
      return <RenderManualMessage content={message.content} />;
  }
};

const RenderManualMessage = (props) => {
  const { content } = props;
  const messageText = { __html: content };
  return <div dangerouslySetInnerHTML={messageText} />;
};

const RenderAppointmentMessage = (props) => {
  const { content } = props;
  const dispatch = useDispatch();
  const appointmentData = JSON.parse(content)[0];
  const appointment = appointmentData.fields;
  const venues = useSelector((state) => state.note.venues);
  appointment.id = appointmentData.pk;
  const [venue, setVenue] = useState(null);
  const booking = useSelector((state) => state.note.bookings[appointment.id]); //Change later for a more efficient way to collect booking for each message. Maybe bring all bookings filtering by pk

  useEffect(() => {
    if (!booking) {
      dispatch(getBookingFromAppointment(appointment));
    }
  }, []);

  useEffect(() => {
    if (booking && !venue && booking.space && venues) {
      setVenue(venues.find((v) => v.id === booking.space.venue));
    }
  }, [booking, venue]);

  return booking ? (
    <div className="text-primary">
      <i>New appointment</i> on
      <span className="body-block ml-1 mt-2">
        {getDateFormat(booking.startDate)},{" "}
        {moment(appointment.start_time, "HH:mm:ss").format("hh:mm A")}-
        {moment(appointment.end_time, "HH:mm:ss").format("hh:mm A")}
      </span>{" "}
      at
      <span className="body-block ml-1">
        {!venue ? "Alternate space" : `${venue.name}, ${booking.space.name}`}
      </span>
    </div>
  ) : (
    ""
  );
};

export default ProviderMessage;
