import React, { Component } from "react";
import { connect } from "react-redux";
import { Input, Button, Row, Card, CardBody, CardFooter } from "reactstrap";
import ReactQuill from "react-quill";
import {
  getProviderMessage,
  clientSendMessage,
} from "../../../../../redux/actions/messageAction";
import { getDateTime, getUserFullName, stripHtml } from "helpers/UtilsHelper";
import ProviderMessage from "./ProviderMessage.jsx";
import ClientMessage from "./ClientMessage.jsx";
import cons from "../../../../../constants";
import "./Messages.scss";

const defaultState = () => {
  return {
    content: "",
    files: [],
    attachments: [],
  };
};
const defaultFilters = () => {
  return {
    ordering: "created_at",
    desc: false,
    client: "",
  };
};

class MainScreen extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
    this.filters = defaultFilters();
  }
  el = React.createRef();

  componentDidMount() {
    const { clientId } = this.props;
    this.filters.client = clientId;
    this.props.getProviderMessage(this.filters, this.scrollToBottom);
    this.getProviderMessage = setInterval(
      () => this.props.getProviderMessage(this.filters),
      5000
    );
  }

  componentWillUnmount() {
    clearInterval(this.getProviderMessage);
  }

  scrollToBottom = () => {
    this.el.scrollIntoView(false);
    this.el.scrollIntoView({ block: "nearest" });
    this.el.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "nearest",
    });
  };

  handleChange = (value) => {
    this.setState({ content: value });
  };

  handleUploadFile = (e) => {
    let tempArr = [];
    if (e.target.files.length) {
      Object.keys(e.target.files).forEach((i) => {
        tempArr.push(e.target.files[i]);
      });
      this.setState({
        files: [...tempArr],
      });
    }
  };

  handleSubmit = () => {
    const { content, files } = this.state;
    if (stripHtml(content)) {
      let data = {
        content,
        files: files,
        client: this.props.clientDetail.id,
      };
      this.props.clientSendMessage(data);
      this.clearData();
    }
  };

  clearData = () => {
    this.setState(defaultState());
  };

  render() {
    const { providerData, messageData, clientDetail } = this.props;
    const clientName = getUserFullName(clientDetail);
    const providerName = getUserFullName(providerData);
    const listMessage = messageData.result;
    const { content, files } = this.state;
    const fileCount = files.length;
    return (
      <div className="main">
        <Card className="mt-3 mx-auto main">
          <CardBody>
            <div
              ref={(el) => {
                this.el = el;
              }}
            >
              {listMessage &&
                listMessage.map((item) => {
                  const content = item.content;
                  const timeCreate = getDateTime(
                    item.createdAt,
                    "MM/DD/YYYY - hh:mm:ss A"
                  );
                  const type = item.fromType || 0;
                  const files = item.files;
                  if (type === cons.FROM_TYPE.CLIENT) {
                    return (
                      <ClientMessage
                        key={item.id}
                        client={clientDetail}
                        timeCreate={timeCreate}
                        content={content}
                        files={files}
                        clientName={clientName}
                      />
                    );
                  } else if (type === cons.FROM_TYPE.PROVIDER) {
                    return (
                      <ProviderMessage
                        key={item.id}
                        provider={providerData}
                        timeCreate={timeCreate}
                        message={item}
                        files={files}
                        providerName={providerName}
                      />
                    );
                  }
                  return "";
                })}
            </div>
          </CardBody>
          <CardFooter>
            <ReactQuill
              className="mt-3"
              value={content}
              onChange={this.handleChange}
              name="content"
              modules={{
                toolbar: [
                  ["bold", "italic", "underline"],
                  [{ list: "ordered" }, { list: "bullet" }],
                  ["link", "blockquote", "code"],
                ],
              }}
            />
            <Row className="my-3 md-ml-2 align-items-center d-flex justify-content-end mr-1">
              <div>
                <Input
                  accept="file/*"
                  id="text-button-file"
                  className="d-none"
                  type="file"
                  multiple
                  onChange={this.handleUploadFile}
                />
                <div className="mr-3 mt-3">
                  <label className="mr-2 text-muted">
                    {fileCount > 0
                      ? fileCount > 1
                        ? `${fileCount} files`
                        : `${fileCount} file`
                      : ""}
                  </label>
                  <label
                    style={{ cursor: "pointer" }}
                    htmlFor="text-button-file"
                  >
                    <i className="fas fa-paperclip fa-lg"></i>
                  </label>
                </div>
              </div>
              <Button
                className="text-right"
                color="info"
                disabled={!content}
                outline
                onClick={this.handleSubmit}
              >
                <i className="fas fa-paper-plane"></i> Send
              </Button>
            </Row>
          </CardFooter>
        </Card>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    providerData: state.navBars.data,
    messageData: state.message.data,
    loading: state.message.loading,
  };
};

const mapActionToProps = { getProviderMessage, clientSendMessage };

export default connect(mapStateToProps, mapActionToProps)(MainScreen);
