import React from "react";
import _ from "lodash";
import { Row } from "reactstrap";
import { getFileNameFromPath } from "../../../../../helpers/FileHelper";
import defaultAvatar from "../../../../../assets/img/theme/default-avatar.png";

const ClientMessage = (props) => {
  const { client, content, files, timeCreate, clientName } = props;

  let initLetter =
    props.client.firstName.charAt(0) + props.client.lastName.charAt(0);

  const message = { __html: content };
  const clientAvatar =
    _.isNull(client) || _.isNull(client.avatar) ? defaultAvatar : client.avatar;
  return (
    <Row className="user d-flex align-items-start justify-content-start">
      <div
        className="user__avatar"
        style={{ display: "flex", width: "100%", justifyContent: "center" }}
      >
        {/* <img src={clientAvatar} className="avatar rounded-circle" alt="AA" /> */}
        <div
          style={{
            display: "flex",
            width: "50px",
            height: "50px",
            background: "gray",
            borderRadius: "50%",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <p style={{ fontSize: "1.6rem", color: "white" }}>{initLetter}</p>
        </div>
        <span className="user__avatar__name" style={{ textAlign: "center" }}>
          {clientName}
        </span>
      </div>
      <div className="msg-box">
        <div
          className="message-content text-left"
          style={{ background: "#ffc9ae", color: "black" }}
        >
          <div dangerouslySetInnerHTML={message} />
          <div className={files.length > 0 ? "file-list mt-3" : "d-none"}>
            <b>Attachments</b>
            {files.length > 0 ? <ListFile files={files} /> : ""}
          </div>
        </div>
        <span className="time_date text-left mr-2 mt-2">{timeCreate}</span>
      </div>
    </Row>
  );
};

export default ClientMessage;

const ListFile = (props) => {
  const { files } = props;
  return (
    files.length > 0 &&
    files.map((file, i) => {
      const fileName = getFileNameFromPath(file.messageFile);
      return (
        <div key={i} className="d-flex justify-content-start">
          <a
            className="file-name"
            href={file.messageFile}
            target="_blank"
            rel="noopener noreferrer"
          >
            <li>{fileName}</li>
          </a>
        </div>
      );
    })
  );
};
