import React from "react";
import { useDispatch } from "react-redux";
import moment from "moment";
import {
  Button,
  Modal,
  Label,
  Col,
  Row,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import ReactDatetime from "react-datetime";
import { getDate } from "helpers/UtilsHelper";
import { toast } from "react-toastify";
import { addPayment } from "../../../../../redux/actions/invoiceAction";
import cons from "../../../../../constants";
import "./PaymentModal.scss";
import { useState } from "react";

const payment_type = { BALANCE: 1, OTHER_AMOUNT: 2 };
const now = moment();

export default function PaymentModal(props) {
  const dispatch = useDispatch();
  const [radio, setRadio] = useState(payment_type.BALANCE);
  const { isOpen, toggle, priceRemain, invoiceId } = props;

  const [date_received, setDate_received] = useState(now);
  const [dropdownPaymentTypeName, setDropdownPaymentTypeName] =
    useState("CASH");
  const [dropdownPaymentTypeOpen, setDropdownPaymentTypeOpen] = useState(false);
  const toggleSelectPaymentType = () => {
    setDropdownPaymentTypeOpen(!dropdownPaymentTypeOpen);
  };
  const [balance, setBalance] = useState("");
  const [paymentType, setPaymentType] = useState(cons.PAYMENT_TYPE.CASH);

  const selectedPayment = (type, value) => {
    if (type === payment_type.BALANCE) {
      setBalance(value);
    }
    setRadio(type);
  };

  const isValidDate = (current) => {
    var yesterday = moment().subtract(1, "day");
    return current.isAfter(yesterday);
  };

  const handleChangePaymentType = (e, itemName) => {
    setPaymentType(e.target.value);
    setDropdownPaymentTypeName(itemName);
  };

  const handleAddPayment = (invoiceId, servicePrice) => {
    const data = {
      status: Number(paymentType),
      date_received: getDate(date_received._d),
      price: Number(balance) || Number(servicePrice),
    };
    if (Number(data.price) > Number(servicePrice)) {
      delete data["price"];
      toast.error("Value could not be greater service price");
    }
    if (Number(data.price) < 0) {
      delete data["price"];
      toast.error("Value could not be negative");
    } else {
      dispatch(addPayment(data, invoiceId));
      toggle();
    }
  };

  return (
    <Modal
      className="modal-dialog-centered payment-modal"
      isOpen={isOpen}
      toggle={toggle}
    >
      <div className="modal-header">
        <h6 className="modal-title text-muted" id="modal-title-default">
          {" "}
          ADD PAYMENT{" "}
        </h6>
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={toggle}
        >
          <span aria-hidden={true}>
            {" "}
            <i className="fas fa-chevron-up" />{" "}
          </span>
        </button>
      </div>
      <div className="modal-body text-left">
        <div className="custom-control custom-radio mb-3">
          <input
            className="custom-control-input"
            id="100_balance"
            name="payment"
            type="radio"
            defaultChecked={radio === payment_type.BALANCE}
            onClick={() => selectedPayment(payment_type.BALANCE, priceRemain)}
          />
          <Label className="custom-control-label" htmlFor="100_balance">
            <b>${priceRemain}: Balance</b>
          </Label>
        </div>
        <Row className="d-flex align-items-center justify-content-between">
          <Col md={6}>
            <div className="custom-control custom-radio">
              <input
                className="custom-control-input"
                id="other_payment"
                name="payment"
                type="radio"
                defaultChecked={radio === payment_type.OTHER_AMOUNT}
                onClick={() => selectedPayment(payment_type.OTHER_AMOUNT)}
              />
              <Label className="custom-control-label" htmlFor="other_payment">
                <b className="text muted">Other Amount</b>
              </Label>
            </div>
          </Col>
          <Col md={6}>
            {radio === payment_type.OTHER_AMOUNT && (
              <InputGroup className="currency">
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <i className="fas fa-dollar-sign"></i>
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="balance"
                  max={priceRemain}
                  min="0"
                  onChange={(e) => setBalance(e.target.value)}
                />
              </InputGroup>
            )}
          </Col>
        </Row>
        <Row className="d-flex align-items-center justify-content-between mt-3">
          <Col md={4} className="order-2 order-md-1 mt-3 mt-md-0">
            <Dropdown
              isOpen={dropdownPaymentTypeOpen}
              toggle={toggleSelectPaymentType}
              className="mb-2 mb-md-0 header__dropdown"
            >
              <DropdownToggle caret>
                {dropdownPaymentTypeName || "choose"}
              </DropdownToggle>
              <DropdownMenu>
                {cons.PAYMENT_TYPE_INFO.map((item, i) => {
                  return (
                    <DropdownItem
                      key={i}
                      name="paymentType"
                      onClick={(e) =>
                        handleChangePaymentType(e, `${item.type}`)
                      }
                      value={item.id}
                    >
                      {item.type}
                    </DropdownItem>
                  );
                })}
              </DropdownMenu>
            </Dropdown>
          </Col>
          <Col md={8} className="date-input pl-0 order-1 order-md-2">
            <InputGroup className="d-flex justify-content-md-end justify-content-start ml-3 ml-md-0">
              <ReactDatetime
                value={date_received}
                timeFormat={false}
                dateFormat={true}
                closeOnSelect={true}
                isValidDate={isValidDate}
                onChange={(date) => setDate_received(date)}
              />
              <InputGroupAddon addonType="prepend">
                <InputGroupText>
                  <i className="fas fa-calendar"></i>
                </InputGroupText>
              </InputGroupAddon>
            </InputGroup>
          </Col>
        </Row>
      </div>
      <div className="modal-footer">
        <Button
          color="primary"
          type="button"
          onClick={() => handleAddPayment(invoiceId, priceRemain)}
          className="w-100"
        >
          Add Payment
        </Button>
      </div>
    </Modal>
  );
}
