import React, { useState } from "react";
import { Row, Col, Button, Input } from "reactstrap";
import ReactQuill from "react-quill";
import ReactDatetime from "react-datetime";
import { ExportToCsv } from "export-to-csv";
import _ from "lodash";
import { getDateFormat, getTime } from "helpers/UtilsHelper";

import cons, { NOTE_APPOINTMENTS } from "../../../../../constants";
import Loading from "../../../../../components/Loading";
import Note from "./Note.jsx";

import "./ClientOverview.scss";
import { useEffect } from "react";
import moment from "moment";
import noteApi from "../../../../../api/noteApi";
import notification from "../../../../../constants/notification";
import { getUserFullName } from "../../../../../helpers/UtilsHelper";
import { useDispatch, useSelector } from "react-redux";
import { getBookingFromAppointment } from "../../../../../redux/actions/noteAction";
import { useLocation } from "react-router-dom";

const ClientOverview = (props) => {
  const {
    handleChange,
    content,
    disableButton,
    validDate,
    datetime,
    fileCount,
    noteList,
    loadingNoteList,
    deleteNote,
    clientId,

    onChangeState,
    handleSubmit,
    handleUploadFile,
    loadMore,
  } = props;

  const [exporting, setExporting] = useState(false);
  let location = useLocation();

  useEffect(() => {
    onChangeState("datetime", moment());
  }, []);

  const filterLatestAppointmentNotes = (notes) => {
    const filteredNotes = [];
    const alreadyParsedAppointments = [];
    if (!notes) {
      return filteredNotes;
    }
    for (let note of notes) {
      if (NOTE_APPOINTMENTS.indexOf(note.noteType) < 0) {
        filteredNotes.push(note);
        continue;
      }
      const appointmentInfo = JSON.parse(note.content)[0];
      if (alreadyParsedAppointments.indexOf(appointmentInfo.pk) < 0) {
        filteredNotes.push(note);
        alreadyParsedAppointments.push(appointmentInfo.pk);
      }
    }
    return filteredNotes;
  };

  const getFileName = () => {
    const fullname = getUserFullName(props.clientDetail);
    return `${fullname} - Notes _ ${moment().valueOf()}.csv`;
  };

  let htmlToText = (text) => {
    var html = text;
    var div = document.createElement("div");
    div.innerHTML = html;
    var text = div.textContent || div.innerText || "";

    return text;
  };

  // let fancyHtmlToText = (content, noteTypes) => {
  //   const parsedContent = JSON.parse(content)[0];
  //   const appointment = parsedContent.fields;
  //   appointment.id = parsedContent.pk;
  //   const venues = useSelector((state) => state.note.venues);
  //   const booking = useSelector((state) => state.note.bookings[appointment.id]);

  //   const dispatch = useDispatch();
  //   const [venue, setVenue] = useState({});

  //   useEffect(() => {
  //     if (booking === undefined) {
  //       dispatch(getBookingFromAppointment(appointment));
  //     }
  //   }, [booking]);

  //   useEffect(() => {
  //     if (booking && !venue.id) {
  //       setVenue(
  //         (venues &&
  //           venues.find((v) => v.id === _.get(booking, "space.venue"))) ||
  //           {}
  //       );
  //     }
  //   }, [booking, venue]);

  //   let html = (
  //     <>
  //       <ul>
  //         <li>
  //           <b>Location: </b>
  //           {venue.name || "Non-Ethera"}
  //         </li>
  //         <li>
  //           <b>Room: </b>
  //           {_.get(booking, "space.name", "Non-Ethera")}
  //         </li>
  //         {/* <li><b>Date: </b>{booking && getDateFormat(booking.startDate)}</li> */}
  //         <li>
  //           <b
  //             style={{ textTransform: "capitalize" }}
  //             className={noteTypes.color}
  //           >
  //             {noteTypes.type}
  //           </b>
  //         </li>
  //         <li>
  //           <b>Start Time: </b>
  //           {booking &&
  //             getTime(`${booking.startDate} ${appointment.start_time}`)}
  //         </li>
  //         <li>
  //           <b>End Time: </b>
  //           {booking && getTime(`${booking.startDate} ${appointment.end_time}`)}
  //         </li>
  //       </ul>
  //     </>
  //   );

  //   var div = document.createElement("div");
  //   div.innerHTML = html;
  //   var text = div.textContent || div.innerText || "";

  //   return text;
  // };

  const handleDownloadNotes = () => {
    if (noteList.result.length > 0) {
      const options = {
        fieldSeparator: ",",
        filename: getFileName(),
        quoteStrings: '"',
        decimalSeparator: ".",
        showLabels: true,
        showTitle: false,
        title: "",
        useTextFile: false,
        useBom: true,
        useKeysAsHeaders: true,
        // headers: ['Column 1', 'Column 2', etc...] <-- Won't work with useKeysAsHeaders present!
      };

      let exportedData = [];

      for (let i = 0; i < noteList.result.length; i++) {
        if (noteList.result[i].noteType === 100) {
          let newObj = {
            Date: moment(noteList.result[i].datetime)
              .format("MM-DD-YYYY")
              .toString(),
            Time: moment(noteList.result[i].datetime)
              .format("hh:mm a")
              .toString(),
            Content:
              noteList.result[i].noteType === 100
                ? htmlToText(noteList.result[i].content)
                : "",
          };
          exportedData.push(newObj);
        }
      }

      const csvExporter = new ExportToCsv(options);

      csvExporter.generateCsv(exportedData);
    }
  };

  const noteData = filterLatestAppointmentNotes(noteList.result);

  return (
    <div className="overview">
      {loadingNoteList && <Loading />}
      <ReactQuill
        value={content}
        onChange={handleChange}
        name="content"
        modules={{
          toolbar: [
            ["bold", "italic", "underline"],
            [
              {
                list: "ordered",
              },
              {
                list: "bullet",
              },
            ],
            ["link", "blockquote", "code"],
          ],
        }}
      />
      <Row className="my-3 md-ml-2 align-items-center">
        <Col xl="8" lg="8" md="8">
          <Row className="align-items-center">
            <span className="d-none d-md-flex ml-3">Date Stamp for Note</span>
            <ReactDatetime
              className="overview__calendar ml-1"
              id="bookingStartDate"
              inputProps={{
                placeholder: "--/--/--  --:-- --",
              }}
              closeOnSelect={true}
              timeFormat={false}
              dateFormat={true}
              isValidDate={validDate}
              value={datetime}
              onChange={(date) => onChangeState("datetime", date)}
            />
            <ReactDatetime
              className="overview__calendar ml-1"
              id="bookingStartDate"
              inputProps={{
                placeholder: "--/--/--  --:-- --",
              }}
              closeOnSelect={true}
              timeFormat={true}
              dateFormat={false}
              isValidDate={validDate}
              value={datetime}
              onChange={(date) => onChangeState("datetime", date)}
            />

            <i className="far fa-calendar-alt d-flex d-md-none mt-2 ml-3" />
          </Row>
        </Col>
        <Col className="text-md-right text-center mt-sm-0 mt-3">
          <Row className="d-flex justify-content-end mr-1">
            <div>
              <Input
                accept="file/*"
                id="text-button-file"
                className="d-none"
                type="file"
                multiple
                onChange={handleUploadFile}
              />
              <div className="mr-3 mt-3">
                <label className="mr-2 text-muted">
                  {fileCount > 0
                    ? fileCount > 1
                      ? `${fileCount} files`
                      : `${fileCount} file`
                    : ""}
                </label>
                <label style={{ cursor: "pointer" }} htmlFor="text-button-file">
                  <i className="fas fa-paperclip fa-lg"></i>
                </label>
              </div>
            </div>
            <Button
              className="text-right"
              color="info"
              onClick={handleSubmit}
              disabled={disableButton}
            >
              Add Note
            </Button>
          </Row>
        </Col>
        {noteData.length && (
          <Col xl="12" lg="12" md="12" className="mt-3">
            <Button
              color="info"
              outline
              disabled={exporting}
              onClick={handleDownloadNotes}
            >
              Download Notes
            </Button>
          </Col>
        )}
      </Row>
      {noteData &&
        noteData.map((item) => {
          const noteTypes = cons.NOTE_TYPE_INFO.find(
            (elm) => elm.id === item.noteType
          );
          return (
            <div className="mt-3 note" key={item.id}>
              <Note item={item} noteTypes={noteTypes} deleteNote={deleteNote} />
            </div>
          );
        })}
      {noteList.result && noteList.result.length >= noteList.count ? (
        ""
      ) : (
        <Row className="d-flex justify-content-center mb-3">
          <Button onClick={loadMore} color="link">
            {" "}
            Load More{" "}
          </Button>
        </Row>
      )}
    </div>
  );
};

export default ClientOverview;
