import React, { Component } from "react";
import { connect } from "react-redux";
import _ from "lodash";
import moment from "moment";
import {
  Container,
  Row,
  Col,
  ButtonGroup,
  Button,
  Input,
  Label,
} from "reactstrap";
import { getUserFullName, getDate, stripHtml } from "helpers/UtilsHelper";
import {
  getClientDetail,
  getList as getClients,
} from "../../../../redux/actions/clientAction";
import { getListInvoice } from "../../../../redux/actions/invoiceAction";
import {
  addNote,
  getNoteList,
  loadMore,
  deleteNote,
  getVenues,
  addAppointment,
  editAppointment,
  deleteAppointment,
  getBookingFromAppointment,
} from "../../../../redux/actions/noteAction";
import {
  cancelAddNewBooking,
  getDayBookings,
} from "../../../../redux/actions/providerDashboardActions";

import ClientInformationOverview from "./ClientOverview/ClientInformationOverview";
import PaginationBar from "../../../../components/PaginationBar";
import ClientOverview from "./ClientOverview/ClientOverview";
import ClientBilling from "./ClientBilling/ClientBilling";
import Messages from "./ClientMessages/Messages";
import "./ClientDetailScreen.scss";
import { toast } from "react-toastify";
import BookingModal from "../../Dashboard/BookingModal";
import { Link } from "react-router-dom";

const tabs = { OVERVIEW: 1, MESSAGE: 2, BILLING: 3 };

const defaultState = () => {
  return {
    radios: tabs.OVERVIEW,
    content: "",
    datetime: "",
    files: [],
    attachments: [],
    start_date: "",
    end_date: "",
  };
};

const defaultFilters = () => {
  return {
    limit: 5,
    offset: 0,
    selected: 0,
    search: "",
    ordering: "created_at",
    desc: false,
    status: "",
    appointment__client: "",
    clientId: "",
  };
};

class ClientDetailScreen extends Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
    this.filters = defaultFilters();
  }

  componentDidMount() {
    const goToMessagesPage = _.get(
      this.props.location.state,
      "goToMessagesPage",
      null
    );
    const goToBillingPage = _.get(
      this.props.location.state,
      "goToBillingPage",
      null
    );
    if (goToMessagesPage) {
      this.setState({ radios: tabs.MESSAGE });
    }
    if (goToBillingPage) {
      this.setState({ radios: tabs.BILLING });
    }
    const clientId = this.props.match.params.id;
    this.setState({ clientId });
    if (!this.props.location.state) {
      this.loadData(clientId);
    }

    this.filters.appointment__client = clientId;
    this.props.getListInvoice(this.filters);
    this.props.getVenues();
    this.props.getClients();
  }

  loadData(clientId) {
    this.props.getClientDetail(clientId);
    this.props.getNoteList(clientId, this.filters);
  }

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.filters.limit = value;
      this.props.getListInvoice(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.props.getListInvoice(this.filters);
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  handleRadios = (radios) => {
    this.setState({ radios });
  };

  handleChangeNote = (value) => {
    this.setState({ content: value });
  };

  onChangeState = (name, value) => {
    try {
      this.setState({ [name]: value });
    } catch (error) {}
  };

  handleReactDatetimeChange = (who, date) => {
    if (
      this.state.start_date &&
      who === "end_date" &&
      new Date(this.state.start_date._d + "") > new Date(date._d + "")
    ) {
      this.setState({
        start_date: date,
        end_date: date,
      });
    } else if (
      this.state.end_date &&
      who === "start_date" &&
      new Date(this.state.end_date._d + "") < new Date(date._d + "")
    ) {
      this.setState({
        start_date: date,
        end_date: date,
      });
    } else {
      this.setState({
        [who]: date,
      });
    }
  };

  getClassNameReactDatetimeDays = (date) => {
    if (this.state.start_date && this.state.end_date) {
    }
    if (
      this.state.start_date &&
      this.state.end_date &&
      this.state.start_date._d + "" !== this.state.end_date._d + ""
    ) {
      if (
        new Date(this.state.end_date._d + "") > new Date(date._d + "") &&
        new Date(this.state.start_date._d + "") < new Date(date._d + "")
      ) {
        return " middle-date";
      }
      if (this.state.end_date._d + "" === date._d + "") {
        return " end-date";
      }
      if (this.state.start_date._d + "" === date._d + "") {
        return " start-date";
      }
    }
    return "";
  };

  loadMore = () => {
    const clientId = this.props.match.params.id;
    const tempOffset = this.filters.offset + this.filters.limit;
    this.filters.offset = tempOffset;
    this.props.loadMore(clientId, this.filters);
  };

  handleUploadFile = (e) => {
    let tempArr = [];
    if (e.target.files.length) {
      Object.keys(e.target.files).forEach((i) => {
        tempArr.push(e.target.files[i]);
      });
      this.setState({
        files: [...tempArr],
      });
    }
  };

  handleSubmit = () => {
    const clientId = this.props.match.params.id;
    let { datetime, content, files } = this.state;
    if (stripHtml(content) || !_.isEmpty(files)) {
      let data = {
        content,
        attachments: files,
      };
      if (datetime) {
        data.datetime = datetime.format();
      }
      this.props.addNote(clientId, data);
      this.clearData();
    }
  };

  filterBilling = () => {
    const { start_date, end_date } = this.state;
    if (_.isString(start_date) || _.isString(end_date)) {
      toast.error("Please pick date");
    } else {
      this.filters = {
        ...this.filters,
        start_date: start_date ? getDate(start_date._d) : "",
        end_date: end_date ? getDate(end_date._d) : "",
      };
      this.props.getListInvoice(this.filters);
    }
  };

  deleteNote = (noteId) => {
    const clientId = this.props.match.params.id;
    this.props.deleteNote(clientId, noteId);
  };

  clearData = () => {
    const { start_date, end_date } = this.state;
    if (start_date || end_date) {
      this.filters = {
        ...this.filters,
        start_date: "",
        end_date: "",
      };
      this.setState({ start_date: "", end_date: "" });
      this.props.getListInvoice(this.filters);
    } else {
      this.setState(defaultState());
    }
    this.setState({ datetime: moment() });
  };

  addAppointment = (data) => {
    this.props.addAppointment(data, (appointment) => {
      this.props.getBookingFromAppointment(appointment);
      this.loadData(this.props.match.params.id);
    });
  };

  editAppointment = (data, callback) => {
    this.props.editAppointment(data, (appointment) => {
      callback();
      this.props.getBookingFromAppointment(appointment);
      this.loadData(this.props.match.params.id);
    });
  };

  deleteAppointment = (data) => {
    this.props.deleteAppointment(data, (appointment) => {
      this.props.getBookingFromAppointment(appointment);
      this.loadData(this.props.match.params.id);
    });
  };

  goBack = () => {
    // this.props.history.push("/provider/clients", { state: "sample data" });

    if ("query" in this.props.history.location) {
      this.props.history.push({
        pathname: "/provider/clients",
        state: { filter: this.props.history.location.query },
      });
    } else {
      this.props.history.push({
        pathname: "/provider/clients",
        state: undefined,
      });
    }
  };

  render() {
    const { radios, content, files, datetime, start_date, end_date, clientId } =
      this.state;
    const {
      clientDetail,
      noteList,
      loadingNoteList,
      invoiceData,
      loadingInvoices,
      clients,
      cancelAddNewBooking,
      getDayBookings,
      dayBookings,
    } = this.props;

    const clientList = clients.result ? clients.result : [];
    const invoiceList = invoiceData.result;
    const fileCount = files.length;
    const disableButton = !_.isEmpty(content)
      ? stripHtml(content) || !_.isEmpty(files)
        ? false
        : true
      : true;
    const { limit, selected } = this.filters;
    const totalPage = Math.ceil(invoiceData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < invoiceData.count
          ? (selected + 1) * limit
          : invoiceData.count,
      total: invoiceData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };
    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };

    return (
      <>
        <Button
          color="info"
          style={{ marginTop: "20px", marginLeft: "20px" }}
          onClick={this.goBack}
        >
          <i className="fa fa-arrow-left" /> Back
        </Button>

        <Container className="mt-2">
          <BookingModal
            venues={[]}
            spaces={[]}
            selectedVenue={null}
            getSpaces={() => {}}
            bookSpace={null}
            cancelAddNewBooking={cancelAddNewBooking}
            clients={clientList}
            addAppointment={this.addAppointment}
            deleteAppointment={this.deleteAppointment}
            editAppointment={this.editAppointment}
            dayBookings={dayBookings}
            getDayBookings={getDayBookings}
          />
          <Col className="text-center" lg="8">
            <Row className="d-flex align-items-center justify-content-start">
              <Link to={`/provider/clients/${clientDetail.id}/edit`}>
                <h1>{getUserFullName(clientDetail)}</h1>
              </Link>
            </Row>
            <Row className="d-flex align-items-center justify-content-start">
              <ButtonGroup
                className="btn-group-toggle mb-3"
                data-toggle="buttons"
              >
                <Button
                  className={`${radios === tabs.OVERVIEW ? "active" : ""}`}
                  color="default"
                  outline
                  onClick={() => this.handleRadios(tabs.OVERVIEW)}
                >
                  <Input
                    name="options"
                    type="radio"
                    value={radios === tabs.OVERVIEW}
                  />
                  Overview
                </Button>
                <Button
                  className={`${radios === tabs.MESSAGE ? "active" : ""}`}
                  color="default"
                  outline
                  onClick={() => this.handleRadios(tabs.MESSAGE)}
                >
                  <Input
                    name="options"
                    type="radio"
                    value={radios === tabs.MESSAGE}
                  />
                  Messages
                </Button>
                <Button
                  className={`radio-customs ${
                    radios === tabs.BILLING ? "active" : ""
                  }`}
                  color="default"
                  outline
                  onClick={() => this.handleRadios(tabs.BILLING)}
                >
                  <Input
                    name="options"
                    type="radio"
                    value={radios === tabs.BILLING}
                  />
                  Billing
                </Button>
              </ButtonGroup>
            </Row>
          </Col>
          <Row className="justify-content-center">
            <Col className="card-wrapper ct-example" md="7" xl="8">
              {radios === tabs.OVERVIEW && (
                <ClientOverview
                  clientId={clientId}
                  clientName={clientDetail}
                  content={content}
                  disableButton={disableButton}
                  datetime={datetime}
                  fileCount={fileCount}
                  noteList={noteList}
                  loadingNoteList={loadingNoteList}
                  clientDetail={clientDetail}
                  handleChange={this.handleChangeNote}
                  validDate={this.validDate}
                  onChangeState={this.onChangeState}
                  handleSubmit={this.handleSubmit}
                  handleUploadFile={this.handleUploadFile}
                  loadMore={this.loadMore}
                  deleteNote={this.deleteNote}
                />
              )}
              {radios === tabs.MESSAGE && clientId && (
                <Messages clientDetail={clientDetail} clientId={clientId} />
              )}
              {radios === tabs.BILLING && (
                <ClientBilling
                  start_date={start_date}
                  end_date={end_date}
                  invoiceList={invoiceList}
                  loadingInvoices={loadingInvoices}
                  clientId={clientId}
                  handleReactDatetimeChange={this.handleReactDatetimeChange}
                  getClassNameReactDatetimeDays={
                    this.getClassNameReactDatetimeDays
                  }
                  filterBilling={this.filterBilling}
                  clearFilterBilling={this.clearData}
                />
              )}
              {radios === tabs.BILLING &&
                invoiceList &&
                invoiceList.length > 0 && (
                  <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0">
                    <Col
                      className="d-flex align-items-baseline  mb-md-0 mb-2"
                      xs="auto"
                    >
                      <Label className="mr-1 text-nowrap" for="selectPageSize">
                        <small>Size</small>
                      </Label>
                      <Input
                        onChange={this.selectPageSize}
                        value={limit}
                        bsSize="md"
                        type="select"
                        name="selectPageSize"
                        id="selectPageSize"
                      >
                        <option value="5">5</option>
                        <option value="10">10</option>
                        <option value="20">20</option>
                        <option value="50">50</option>
                      </Input>
                      <Label className="ml-1 text-nowrap">
                        {/* <small>
                      {`Showing ${paginationInfo.from} ${
                        paginationInfo.getShowOne()
                          ? ""
                          : `to ${paginationInfo.to}`
                      } of ${paginationInfo.total} entries`}
                    </small> */}

                        <small>
                          Page {`${selected + 1}`} of {`${totalPage}`}
                        </small>
                      </Label>
                    </Col>
                    <Col className="d-flex justify-content-md-end justify-content-sm-start">
                      {totalPage > 1 && (
                        <PaginationBar
                          setting={paginationSetting}
                          handleClick={this.selectPage}
                        />
                      )}
                    </Col>
                  </Row>
                )}
            </Col>
            <Col md="5" xl="4">
              <ClientInformationOverview clientDetail={clientDetail} />
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    clientDetail: state.client.data,
    loading: state.client.loading,
    noteList: state.note.data,
    loadingNoteList: state.note.loading,
    invoiceData: state.invoice.data,
    loadingInvoices: state.invoice.loading,
    clients: state.client.list,
    dayBookings: state.providerDashboard.dayBookings,
  };
};

const mapActionToProps = {
  getClientDetail,
  addNote,
  getNoteList,
  loadMore,
  deleteNote,
  getListInvoice,
  getVenues,
  getClients,
  cancelAddNewBooking,
  addAppointment,
  editAppointment,
  deleteAppointment,
  getBookingFromAppointment,
  getDayBookings,
};

export default connect(mapStateToProps, mapActionToProps)(ClientDetailScreen);
