import React, { useState } from "react";
import { Link } from "react-router-dom";
import _ from "lodash";
import { Row, Col, Badge, Button } from "reactstrap";
import { getDateFormat, getStatusInfo } from "helpers/UtilsHelper";
import { updateInvoiceStatus } from "../../../../../redux/actions/invoiceAction";
import cons from "../../../../../constants";
import PaymentModal from "./PaymentModal";
import ConfirmDeleteModal from "../../../../../components/Modal/ConfirmDeleteModal.jsx";
import Payment from "./Payment.jsx";
import { useDispatch } from "react-redux";

function Billing(props) {
  const dispatch = useDispatch();
  const { item, clientId } = props;
  const status = getStatusInfo(cons.INVOICE_STATUS_INFO, item.status);
  const [isAddPayment, setIsAddPayment] = useState(false);
  const toggleAddPayment = () => setIsAddPayment(!isAddPayment);
  const [isOpenDetail, setOpenDetail] = useState(false);
  const toggleOpenDetail = () => setOpenDetail(!isOpenDetail);
  const [showModal, setShowModal] = useState(false);
  const toggleConfirmCancel = () => setShowModal(!showModal);

  const cancelInvoice = () => {
    let { id } = item;
    let status = cons.INVOICE_STATUS.VOID;
    dispatch(updateInvoiceStatus(status, id));
    toggleConfirmCancel();
  };

  let handelEmailInvoice = () => {
    console.log("Email Invoice");
  };

  return (
    <>
      <PaymentModal
        className="modal-dialog-centered"
        isOpen={isAddPayment}
        toggle={toggleAddPayment}
        invoiceId={item.id}
        priceRemain={item.priceRemain}
        handle={() => alert('add payment for "id"')}
        content={`Do you want to delete this note?`}
      />
      <ConfirmDeleteModal
        showTitle={false}
        className="modal-dialog-centered"
        isOpen={showModal}
        toggle={toggleConfirmCancel}
        handle={cancelInvoice}
        content={`Are you sure you want to cancel this invoice?`}
      />
      <tr className="billing">
        <td className="w-175">
          <Col className="date-time">
            <b>{getDateFormat(item.createdAt, "MMM")}</b>
            <b className="date">{getDateFormat(item.createdAt, "DD")}</b>
          </Col>
        </td>
        <td>
          <Row>
            <b>Invoice</b>
          </Row>
          <Row>
            <Link
              to={{
                pathname: `/provider/clients/${clientId}/invoice-detail/${item.id}`,
                query: `/provider/clients/${clientId}`,
              }}
            >
              <b>INV #{item.invoiceNumber}</b>
            </Link>
          </Row>
        </td>
        <td className="d-flex justify-content-center w-175">
          <Badge color={status.color} pill className="cursor-pointer">
            <b>{status.status || "Unknown"}</b>
          </Badge>
        </td>
        <td className="cursor-pointer">
          {cons.INVOICE_STATUS.UNPAID === item.status && (
            <Col className="d-flex flex-column font-weight-bold">
              <Row className="text-info mb-2" onClick={toggleAddPayment}>
                Add Payment
              </Row>
              <Row className="text-danger mb-2" onClick={toggleConfirmCancel}>
                Void Invoice
              </Row>
              <Row className="text-success" onClick={handelEmailInvoice}>
                Email Invoice
              </Row>
            </Col>
          )}
        </td>
        <td className="text-center w-175">
          <div className="font-weight-bold">${item.priceRemain}</div>
          {!_.isEmpty(item.clientPayments) && (
            <Button color="link" onClick={toggleOpenDetail}>
              {!isOpenDetail ? (
                <i className="fas fa-chevron-down fa-sm" />
              ) : (
                <i className="fas fa-chevron-up fa-sm" />
              )}
            </Button>
          )}
        </td>
      </tr>
      {item.clientPayments &&
        item.clientPayments.map((pay) => {
          return (
            <Payment
              key={pay.id}
              payment={pay}
              isOpen={isOpenDetail}
              invoiceNumber={item.invoiceNumber}
            />
          );
        })}
    </>
  );
}

export default Billing;
