import React from 'react';
import { getDateFormat } from 'helpers/UtilsHelper';
import cons from '../../../../../constants'

export default function Payment(props) {
    const { payment, isOpen } = props
    const paymentType = cons.PAYMENT_TYPE_INFO.find(item => item.id === payment.status)
    return (
        <tr className={`payment ${isOpen ? '' : "d-none"}`}>
            <td colSpan="2"><span className="text-primary">{getDateFormat(payment.createdAt)}</span></td>
            <td className="text-center text-primary"><b>Payment</b> | {paymentType.type}</td>
            <td />
            <td className="text-center text-primary"> {payment.price > 0 ? `+$${payment.price}` : `${payment.price}`} </td>
        </tr>
    )
}