import React from "react";
import { Row, Col, Button, Table } from "reactstrap";
import ReactDatetime from "react-datetime";
import Loading from "../../../../../components/Loading";
import "./ClientBilling.scss";
import Billing from "./Billing";

const ClientBilling = (props) => {
  const {
    start_date,
    end_date,
    invoiceList,
    loadingInvoices,
    clientId,

    handleReactDatetimeChange,
    getClassNameReactDatetimeDays,
    filterBilling,
    clearFilterBilling,
  } = props;

  return (
    <div className="billing border-top">
      {loadingInvoices && <Loading />}
      <Row className="mt-3 ml-0 align-items-center ">
        <Col sm="12" md="4" className="pl-0">
          <ReactDatetime
            className="mb-2"
            inputProps={{
              placeholder: "-- -- --",
            }}
            closeOnSelect={true}
            value={start_date}
            timeFormat={false}
            dateFormat={"MM/DD/YYYY"}
            onChange={(e) => handleReactDatetimeChange("start_date", e)}
            renderDay={(props, currentDate, selectedDate) => {
              let classes = props.className;
              classes += getClassNameReactDatetimeDays(currentDate);
              return (
                <td {...props} className={classes}>
                  {currentDate.date()}
                </td>
              );
            }}
          />
        </Col>

        <Col sm="12" md="4" className="pl-0">
          <ReactDatetime
            className="ml-md-3 w-100 mb-2"
            inputProps={{
              placeholder: "-- -- --",
            }}
            closeOnSelect={true}
            value={end_date}
            timeFormat={false}
            dateFormat={"MM/DD/YYYY"}
            onChange={(e) => handleReactDatetimeChange("end_date", e)}
            renderDay={(props, currentDate, selectedDate) => {
              let classes = props.className;
              classes += getClassNameReactDatetimeDays(currentDate);
              return (
                <td {...props} className={classes}>
                  {currentDate.date()}
                </td>
              );
            }}
          />
        </Col>
        <Col
          sm="12"
          md="4"
          className="d-flex justify-content-md-start justify-content-center mt-3 mt-md-0 pl-0"
        >
          <div>
            <Button
              className="ml-md-3 mb-2"
              color="info"
              onClick={filterBilling}
            >
              {" "}
              View
            </Button>
            <Button
              className="ml-md-3 mb-2"
              color="danger"
              onClick={clearFilterBilling}
            >
              {" "}
              Clear
            </Button>
          </div>
        </Col>
      </Row>
      <Table className="align-items-start table-sort mt-3" responsive>
        <tbody>
          {invoiceList &&
            invoiceList.map((item) => (
              <Billing item={item} key={item.id} clientId={clientId} />
            ))}
        </tbody>
      </Table>
    </div>
  );
};

export default ClientBilling;
