import React, { useState } from "react";
import _ from "lodash";
import { Collapse, Row, Col, Button } from "reactstrap";

import { getFileNameFromPath } from "../../../../../helpers/FileHelper";
import cons, { NOTE_APPOINTMENTS } from "../../../../../constants";
import { getDateFormat, getTime } from "helpers/UtilsHelper";
import ConfirmDeleteModal from "../../../../../components/Modal/ConfirmDeleteModal.jsx";
import "./Note.scss";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getBookingFromAppointment } from "../../../../../redux/actions/noteAction";
import Loading from "../../../../../components/Loading";
import { editAppointmentModal } from "../../../../../redux/actions/providerDashboardActions";

const Note = (props) => {
  const { item, noteTypes, deleteNote } = props;

  const [isOpen, setIsOpen] = useState(false);
  const toggle = () => setIsOpen(!isOpen);
  const [isDelete, setIsDelete] = useState(false);
  const toggleDelete = () => setIsDelete(!isDelete);

  const getTitle = (type) => {
    if (NOTE_APPOINTMENTS.indexOf(type.id) >= 0) {
      return "appointment date";
    }
    return type.type;
  };

  const getColorClass = (type) => {
    if (NOTE_APPOINTMENTS.indexOf(type.id) >= 0) {
      return "text-primary font-weight-bold";
    }
    return type.color;
  };

  return (
    <>
      <ConfirmDeleteModal
        className="modal-dialog-centered"
        isOpen={isDelete}
        toggle={toggleDelete}
        handle={() => deleteNote(item.id)}
        content={`Do you want to delete this note?`}
      />
      <Row className="d-flex align-items-start justify-content-center note">
        <NoteDate item={item} />
        <Col xl="10" sm="8" xs="7">
          <Row className="align-items-center mb-3 ml-0">
            <span className={`text-capitalize ${getColorClass(noteTypes)}`}>
              {/* {getTitle(noteTypes)} */}
              Notes
            </span>
            <small className="ml-2">
              {Number(item.noteType === cons.NOTE_TYPE.MANUAL)
                ? getTime(item.datetime)
                : ""}
            </small>
          </Row>
          <Collapse isOpen={isOpen}>
            <RenderContent item={item} noteTypes={noteTypes} visible={isOpen} />
            <div
              className={
                item.noteAttachments.length > 0 ? "note__file-list" : "d-none"
              }
            >
              <b>Attachments</b>
              <ListFile attachments={item.noteAttachments} />
            </div>
          </Collapse>
        </Col>
        <Col className="text-center" xs="1">
          {!isOpen ? (
            <i
              className="fas fa-chevron-down fa-lg custom__icon__show-detail"
              onClick={toggle}
            />
          ) : (
            <i
              className="fas fa-chevron-up fa-lg custom__icon__show-detail"
              onClick={toggle}
            />
          )}
          {!isOpen && (
            <i
              className="far fa-trash-alt mt-3 custom__icon__delete"
              onClick={toggleDelete}
            />
          )}
        </Col>
      </Row>
    </>
  );
};

const NoteDate = (props) => {
  return NOTE_APPOINTMENTS.indexOf(props.item.noteType) < 0 ? (
    <NormalNoteDate item={props.item} />
  ) : (
    <AppointmentNoteDate item={props.item} />
  );
};

const NormalNoteDate = (props) => {
  return (
    <Col className="note__date-time" xl="1" sm="2" xs="3">
      <b>{getDateFormat(props.item.datetime, "MMM")}</b>
      <b className="note__date-time__day">
        {getDateFormat(props.item.datetime, "DD")}
      </b>
    </Col>
  );
};

const AppointmentNoteDate = (props) => {
  const content = JSON.parse(props.item.content)[0];
  const booking = useSelector((state) => state.note.bookings[content.pk]);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!booking) {
      const appointment = { ...content.fields };
      appointment.id = content.pk;
      dispatch(getBookingFromAppointment(appointment));
    }
  }, [booking]);

  return (
    <Col className="note__date-time" xl="1" sm="2" xs="3">
      <b>{booking && getDateFormat(booking.startDate, "MMM")}</b>
      <b className="note__date-time__day">
        {booking && getDateFormat(booking.startDate, "DD")}
      </b>
    </Col>
  );
};

const ListFile = (props) => {
  const { attachments } = props;
  return attachments.map((file, i) => {
    const fileName = getFileNameFromPath(file.url);
    return (
      <div key={i} className="d-flex justify-content-start">
        <a
          className="wrapper__info__file-name"
          href={file.url}
          target="_blank"
          rel="noopener noreferrer"
        >
          <li>{fileName}</li>
        </a>
      </div>
    );
  });
};

const RenderContent = (props) => {
  const { item, visible, noteTypes } = props;

  switch (Number(item.noteType)) {
    case cons.NOTE_TYPE.MANUAL:
      return <RenderManualNote content={item.content} />;
    case cons.NOTE_TYPE.APPOINTMENT_CREATED:
      return (
        <RenderAppointmentNote
          content={item.content}
          noteTypes={noteTypes}
          visible={visible}
        />
      );
    case cons.NOTE_TYPE.APPOINTMENT_UPDATED:
      return (
        <RenderAppointmentNote
          content={item.content}
          noteTypes={noteTypes}
          visible={visible}
        />
      );
    default:
      return <RenderManualNote content={item.content} />;
  }
};

const RenderManualNote = (props) => {
  const { content } = props;
  const note = { __html: content };

  return <div dangerouslySetInnerHTML={note} />;
};

const RenderAppointmentNote = (props) => {
  const { content, visible, noteTypes } = props;
  const parsedContent = JSON.parse(content)[0];
  const appointment = parsedContent.fields;
  appointment.id = parsedContent.pk;
  const venues = useSelector((state) => state.note.venues);
  const booking = useSelector((state) => state.note.bookings[appointment.id]);
  const [loading, setLoading] = useState(false);
  const dispatch = useDispatch();
  const [venue, setVenue] = useState({});

  useEffect(() => {
    if (visible && booking === undefined) {
      dispatch(getBookingFromAppointment(appointment));
    }
    setLoading(visible && booking === undefined);
  }, [visible, booking]);

  useEffect(() => {
    if (booking && !venue.id) {
      setVenue(
        (venues &&
          venues.find((v) => v.id === _.get(booking, "space.venue"))) ||
          {}
      );
    }
  }, [booking, venue]);

  return loading ? (
    <Loading />
  ) : (
    <>
      <ul>
        <li>
          <b>Location: </b>
          {venue.name || "Non-Ethera"}
        </li>
        <li>
          <b>Room: </b>
          {_.get(booking, "space.name", "Non-Ethera")}
        </li>
        {/* <li><b>Date: </b>{booking && getDateFormat(booking.startDate)}</li> */}
        <li>
          <b
            style={{ textTransform: "capitalize" }}
            className={noteTypes.color}
          >
            {noteTypes.type}
          </b>
        </li>
        <li>
          <b>Start Time: </b>
          {booking && getTime(`${booking.startDate} ${appointment.start_time}`)}
        </li>
        <li>
          <b>End Time: </b>
          {booking && getTime(`${booking.startDate} ${appointment.end_time}`)}
        </li>
      </ul>
      <div className="text-center">
        <Button onClick={() => dispatch(editAppointmentModal(booking))}>
          View appointment
        </Button>
      </div>
    </>
  );
};

export default Note;
