import React, { useState } from "react";
import {
  Row,
  Col,
  Button,
  Card,
  CardHeader,
  CardBody,
  Collapse,
} from "reactstrap";
import { Link } from "react-router-dom";
import { getDateFormat, getDateTime } from "helpers/UtilsHelper";
import cons from "../../../../../constants";
import "./ClientInformationOverview.scss";

const Information = (props) => {
  const { title, content } = props;
  return (
    <Row className="my-2">
      <Col>
        <small>{title}</small>
      </Col>
      <Col className="text-right mr-2 justify-content-end">
        <small className="text-primary">{content || "Unknown"}</small>
      </Col>
    </Row>
  );
};

const ClientInformationOverview = (props) => {
  const { clientDetail } = props;

  const relationship = clientDetail.relationshipStatus
    ? cons.RELATIONSHIP_INFO.find(
        (item) => item.id === Number(clientDetail.relationshipStatus)
      ).relationshipStatus
    : "";
  const employment = clientDetail.employmentStatus
    ? cons.EMPLOYMENT.find(
        (item) => item.id === Number(clientDetail.employmentStatus)
      ).employmentStatus
    : "";
  const language =
    clientDetail.preferredLanguage &&
    cons.LANGUAGE.find((item) => item.code === clientDetail.preferredLanguage)
      .preferredLanguage;

  const gender =
    clientDetail.gender >= 0
      ? cons.GENDER.find((item) => item.id === Number(clientDetail.gender))
          .gender
      : "";

  const race = clientDetail.race
    ? cons.RACE.find((item) => item.id === Number(clientDetail.race)).race
    : "";

  const [isOpen, setIsOpen] = useState(true);
  const toggle = () => setIsOpen(!isOpen);

  let employmentStat = ["Full-time", "Part-time", "Unemployed", "N/A"];

  let getFormatPhone = (text) => {
    if (!text || text === "null" || text === "undefined") {
      return "";
    } else {
      let newText =
        text.substring(0, 3) +
        "-" +
        text.substring(3, 6) +
        "-" +
        text.substring(6, text.length);
      return newText;
    }
  };

  return (
    <Card className="client-info">
      <CardHeader className="client-info__card__header">
        <Row className="d-flex align-items-center">
          <Col>
            <span className="text-uppercase"> client info</span>
          </Col>
          <Col>
            <Button color="link">
              <Link to={`/provider/clients/${clientDetail.id}/edit`}>Edit</Link>
            </Button>
          </Col>
          <Col className="mr-5" xs="1">
            <Button color="link" onClick={toggle}>
              {!isOpen && <i className="fas fa-chevron-down fa-lg" />}
              {isOpen && <i className="fas fa-chevron-up fa-lg" />}
            </Button>
          </Col>
        </Row>
      </CardHeader>
      <Collapse isOpen={isOpen}>
        <CardBody className="client-info__card__body">
          <Information
            title="Phone"
            content={getFormatPhone(clientDetail.phoneNumber)}
          />
          <Information title="Email" content={clientDetail.email} />
          <Information title="Address" content={clientDetail.address} />
          <Information title="City" content={clientDetail.city} />
          <Information title="State" content={clientDetail.state} />
          <Information title="Zip Code" content={clientDetail.zipcode} />

          <Information title="Gender" content={gender} />
          <Information
            title="Started"
            content={getDateTime(clientDetail.firstSeenAt, "MM/DD/YYYY ")}
          />
          <Information
            title="Date of Birth"
            content={getDateFormat(clientDetail.dateOfBirth, "MM/DD/YYYY ")}
          />
          <Information title="Race" content={race} />
          <Information title="Marital Status" content={relationship} />
          <Information
            title="Employment"
            content={employmentStat[clientDetail.employmentStatus]}
          />
          <Information title="Language" content={language} />
        </CardBody>
      </Collapse>
    </Card>
  );
};

export default ClientInformationOverview;
